'use client';
import React, { useState, useEffect } from 'react';
import { API_BASE, DEV_TOKEN } from '../../../../lib/env';
import { Shield, Upload, CheckCircle, Loader2, Key } from 'lucide-react';

export default function AccessPage({ params }: { params: Promise<{ id: string }> }) {
  const [orgId, setOrgId] = useState<string>('');
  const [pem, setPem] = useState('');
  const [busy, setBusy] = useState(false);
  const [result, setResult] = useState<string | null>(null);

  useEffect(() => {
    params.then((p) => setOrgId(p.id));
  }, [params]);

  async function upload() {
    if (!pem) return;
    setBusy(true);
    setResult(null);
    try {
      const res = await fetch(`${API_BASE}/api/certs/${orgId}/upload`, {
        method: 'POST',
        headers: { 'content-type': 'application/json', authorization: `Bearer ${DEV_TOKEN}` },
        body: JSON.stringify({ pem }),
      }).then((r) => r.json());
      setResult('Сертификат успешно загружен');
      setPem('');
    } catch (error: any) {
      setResult(`Ошибка: ${error.message}`);
    } finally {
      setBusy(false);
    }
  }

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-4xl font-bold bg-gradient-to-r from-blue-400 via-green-400 to-purple-400 bg-clip-text text-transparent mb-2">
          Доступ и роли
        </h1>
        <p className="text-gray-400 text-lg">Управление доступом и сертификатами X.509</p>
      </div>

      <div className="p-6 rounded-xl border border-gray-800/50 glass card-hover bg-gradient-to-br from-indigo-500/10 to-indigo-600/5">
        <div className="flex items-center gap-3 mb-4">
          <div className="p-2 rounded-lg bg-indigo-500/20 text-indigo-400">
            <Key className="w-5 h-5" />
          </div>
          <h2 className="text-lg font-semibold text-white">Загрузка X.509 сертификата</h2>
        </div>
        <div className="space-y-4">
          <textarea
            className="w-full border border-gray-700 rounded-lg p-3 bg-gray-800/50 text-gray-200 placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-indigo-500/50 font-mono text-sm h-40"
            placeholder="-----BEGIN CERTIFICATE-----&#10;...&#10;-----END CERTIFICATE-----"
            value={pem}
            onChange={(e) => setPem(e.target.value)}
          />
          <button
            onClick={upload}
            disabled={busy || !pem}
            className="w-full px-4 py-3 rounded-lg bg-gradient-to-r from-indigo-600 to-indigo-500 text-white font-medium hover:from-indigo-500 hover:to-indigo-400 disabled:opacity-50 disabled:cursor-not-allowed transition-all flex items-center justify-center gap-2"
          >
            {busy ? (
              <>
                <Loader2 className="w-4 h-4 animate-spin" />
                <span>Загрузка...</span>
              </>
            ) : (
              <>
                <Upload className="w-4 h-4" />
                <span>Загрузить сертификат</span>
              </>
            )}
          </button>
        </div>
      </div>

      {result && (
        <div className="p-4 rounded-xl border border-green-500/20 bg-green-500/10 text-green-400 flex items-center gap-2">
          <CheckCircle className="w-5 h-5" />
          {result}
        </div>
      )}
    </div>
  );
}

